import createHTMLElement from "../helpers/helpers";
import lunch from "./lunch.png";
import dinner from "./dinner.png";
import beverages from "./beverages.png";
import "./menu.css";

function createMenuItem(name, description, price) {
  const itemName = createHTMLElement("h4", "", name);
  const itemDescription = createHTMLElement("p", "", description);
  const itemPrice = createHTMLElement("div", "", price);
  const itemContainer = createHTMLElement(
    "div",
    { class: "item-container" },
    itemName,
    itemDescription,
    itemPrice
  );

  return itemContainer;
}

function createMenu() {
  const main = document.querySelector("main");

  const lunchImg = createHTMLElement("img", { src: lunch, alt: "lunch icon" });
  const lunchHeading = createHTMLElement("h2", "", "Lunch");
  const lunchHeadingContainer = createHTMLElement(
    "div",
    { class: "menu-heading" },
    lunchImg,
    lunchHeading
  );
  const firstLunchItem = createMenuItem("Item", "this is yummy", "$3.99");
  const secondLunchItem = createMenuItem("Item", "this is yummy", "$3.99");
  const thirdLunchItem = createMenuItem("Item", "this is yummy", "$3.99");
  const lunchContainer = createHTMLElement(
    "div",
    { class: "menu-container" },
    lunchHeadingContainer,
    firstLunchItem,
    secondLunchItem,
    thirdLunchItem
  );

  const dinnerImg = createHTMLElement("img", {
    src: dinner,
    alt: "dinner icon",
  });
  const dinnerHeading = createHTMLElement("h2", "", "Dinner");
  const dinnerHeadingContainer = createHTMLElement(
    "div",
    { class: "menu-heading" },
    dinnerImg,
    dinnerHeading
  );
  const firstDinnerItem = createMenuItem("Item", "this is yummy", "$3.99");
  const secondDinnerItem = createMenuItem("Item", "this is yummy", "$3.99");
  const thirdDinnerItem = createMenuItem("Item", "this is yummy", "$3.99");
  const dinnerContainer = createHTMLElement(
    "div",
    { class: "menu-container" },
    dinnerHeadingContainer,
    firstDinnerItem,
    secondDinnerItem,
    thirdDinnerItem
  );

  const beverageImg = createHTMLElement("img", {
    src: beverages,
    alt: "beverage icon",
  });
  const beverageHeading = createHTMLElement("h2", "", "Beverages");
  const beverageHeadingContainer = createHTMLElement(
    "div",
    { class: "menu-heading" },
    beverageImg,
    beverageHeading
  );
  const firstBeverageItem = createMenuItem("Item", "this is yummy", "$3.99");
  const secondBeverageItem = createMenuItem("Item", "this is yummy", "$3.99");
  const thirdBeverageItem = createMenuItem("Item", "this is yummy", "$3.99");
  const beverageContainer = createHTMLElement(
    "div",
    { class: "menu-container" },
    beverageHeadingContainer,
    firstBeverageItem,
    secondBeverageItem,
    thirdBeverageItem
  );

  main.append(lunchContainer, dinnerContainer, beverageContainer);
}

export default createMenu;
